.cost-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.8rem;
    margin: 1rem 0;
    background-color: whitesmoke;
}

.cost-item__description {
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1;
    margin-top: 0.6rem;
    margin-bottom: 0.8rem;
}

.cost-item h2 {
    color: #3a3a3a;
    font-size: 0.8rem;
    flex: 1;
    margin: 0 1rem;
}

.cost-item__price {
    font-size: 0.8rem;
    font-weight: bold;
    color: #010101;
    background-color: white;
    border: 1px solid darkcyan;
    padding: 0.5rem;
    border-radius: 12px;
    margin-right: 0.5rem;
}

.cost-date {
    display: flex;
    flex-direction: column;
    width: 5rem;
    height: 5rem;
    border: 1px solid darkcyan;
    border-radius: 1.2rem;
    background-color: lightcyan;
    align-items: center;
    justify-content: center;
}

.cost-date__month {
    font-size: 0.75rem;
    font-weight: bold;
    color: darkcyan;
}

.cost-date__year {
    font-size: 0.75rem;
    color: darkcyan;
}

.cost-date__day {
    font-size: 1.5rem;
    font-weight: bold;
}

@media (min-width: 580px) {
    .cost-item__description {
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        flex: 1;
    }

    .cost-item__description h2 {
        font-size: 1rem;
    }

    .cost-item__price {
        font-size: 1rem;
        padding: 0.5rem 1.5rem;
    }


}