.costs-filter {
    color: #111;
    padding: 1rem;
}

.costs-filter__control {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
}

.costs-filter label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    padding-right: 1rem;
}

.costs-filter select {
    font: inherit;
    padding: 0.5rem 3rem;
    font-weight: bold;
    border-radius: 0.5rem;
}



