.new-cost {
    background-color: whitesmoke;
    padding: 1rem;
    margin: 2rem auto;
    width: 70rem;
    max-width: 95%;
    border-radius: 0.5rem;
    text-align: center;
    box-shadow: 0 1px 7px rgba(0, 0, 0, 0.25);
}

.new-cost button {
    font: inherit;
    font-weight: bold;
    cursor: pointer;
    padding: 1rem 2rem;
    border: 1px solid darkcyan;
    background-color: darkcyan;
    color: whitesmoke;
    border-radius: 0.5rem;
    margin-right: 3px;
}

.new-cost button:hover,
.new-cost button:active {
    background-color: rgb(178, 222, 222);
    border-color: rgb(178, 222, 222);
}